<template>
    <div>
        <span style="font-size: small;" id="status">connecting...</span>
        <p style="text-align: right; font-size: small;">
            built by <a href="https://www.linkedin.com/in/vasu-kushwah-39710812b">Vasu Kushwah</a> <a href="https://github.com/vasukushwah">GitHub</a>
        </p>
        <div id="terminal"></div>
    </div>
</template>

<script>    
import { Terminal } from 'xterm'    
import { FitAddon } from 'xterm-addon-fit'
export default {
  name: 'terminal',
  mounted () {
    let _this = this

    this.term = new Terminal({
        cursorBlink: true,
        macOptionIsMeta: true,
        scrollback: true,
    });
    this.terminalid = document.getElementById('terminal')
    this.fitAddon = new FitAddon()
    this.term.loadAddon(this.fitAddon)
    this.term.open(this.terminalid)
    this.fitAddon.fit()
    let url = window.location.href 
    let wsUrl = url.replace('http','ws')
    // let wsUrl = "ws://localhost:5000/"
    this.ws = new WebSocket(wsUrl+ 'term')
    const status = document.getElementById("status") 

    this.ws.onopen = function() {
        _this.ToggleFullscreen();
        status.innerHTML = ''
    }
    this.ws.onmessage = function (evt) {
        if (!window.TextDecoder) {
            _this.read_as_text_with_encoding(evt.data, _this.decoder);
        } else {
            _this.read_as_text_with_decoder(evt.data, _this.decoder);
        }
    }
    this.ws.onclose = function (){
        _this.term.dispose();
        _this.term = undefined;
        _this.ws = undefined;
        status.innerHTML = '<span style="background-color: red;">Disconnected</span>'

    }
    this.term.onData(function(data){
        _this.ws.send(JSON.stringify({ 'data': data }))
    })
    this.term.on_resize = function(cols, rows) {
        if (cols !== _this.cols || rows !== _this.rows) {
            _this.term.resize(cols, rows);
            _this.ws.send(JSON.stringify({ 'resize': [cols, rows] }));
        }
    }
    window.onresize = function(){
        if (_this.term) {
            _this.ResizeTerminal();
        }
    }
    

  },
  data () {
    return {
        style:{},
        termresized:false,
        terminalid:null,
        ws : null,
        encoding :'utf-8', 
        decoder : window.TextDecoder ? new window.TextDecoder(this.encoding) : this.encoding,
        term : null,
        fitAddon : null
    }
},
methods: {
    set_encoding (new_encoding) {
        console.log(new_encoding);
        console.log("Encoding:",this.encoding)
        if (!window.TextDecoder) {
                this.decoder = new_encoding;
                this.encoding = this.decoder;
                console.log('Set encoding to ' + this.encoding);
            } else {
                try {
                    this.decoder = new window.TextDecoder(new_encoding);
                    this.encoding = this.decoder.encoding;
                    console.log('Set encoding to ' + this.encoding);
                } catch (RangeError) {
                    console.log('Unknown encoding ' + this.new_encoding);
                    return false;
                }
            }
    },
    read_file_as_text(file, decoder) {
        if (!window.TextDecoder) {
            this.read_as_text_with_encoding(file,decoder);
        } else {
            this.read_as_text_with_decoder(file,decoder);
        }
    },
    read_as_text_with_encoding(file,encoding) {

        let Term = this.term
        var reader = new window.FileReader()

        if (encoding === undefined) {
            encoding = 'utf-8';
        }
        reader.onload = function() {
                Term.write(reader.result);
        }
        reader.onerror = function(e) {
            console.error(e);
        }
        reader.readAsText(file, encoding);
    },

    read_as_text_with_decoder(file, decoder) {
        
        let _this = this
        var reader = new window.FileReader();
        if (decoder === undefined) {
            decoder = new window.TextDecoder('utf-8', { 'fatal': true });
        }

        reader.onload = function() {
           
            var text;
            try {
                text = decoder.decode(reader.result);
            } catch (TypeError) {
                console.log('Decoding error happened.');
            } finally {
                _this.TermWrite(text)
                   
                
            }
        }
        reader.onerror = function(e) {
            console.error(e);
        }
        reader.readAsArrayBuffer(file)
    },
    ToggleFullscreen(){
        this.terminalid.classList.toggle("fullscreen");
        this.fitAddon.fit()
        this.term.focus()
    },
    CurrentGeometry(){
        if (!this.style.width || !this.style.height) {
            try {
                this.style.width = this.term._core._renderService._renderer.dimensions.actualCellWidth;
                this.style.height = this.term._core._renderService._renderer.dimensions.actualCellHeight;
            } catch (TypeError) {
                console.log("error")
            }
        }
        var cols = parseInt(window.innerWidth / this.style.width, 10) - 1;
        var rows = parseInt(window.innerHeight / this.style.height, 10);
        return { 'cols': cols, 'rows': rows };
    },
    ResizeTerminal(){
        var geometry = this.CurrentGeometry();
        this.term.on_resize(geometry.cols, geometry.rows);
    },
    TermWrite(text){
        this.term.write(text)
        if (!this.termresized) {
                this.ResizeTerminal()
                this.termresized = true;
                }

    }
}

}
</script>

<style>
@import '../assets/css/bootstrap.min.css';
@import '../assets/css/xterm.min.css';
@import '../assets/css/fullscreen.min.css';
p {
    margin-top: 0;
    margin-bottom: 0rem;
}
</style>
